* {
    margin: 0;
    padding: 0;
    border: none;
    transition: all linear .5s;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;
}

.App{
    width: 45rem;
    margin: auto;
    background-image: url(../public/images/Group7562.png);

    
    
}
.admin{
 max-width: 1200px;
 display: flex;
 justify-content: center;
 margin: 0 auto;
 .adminApp{
    width: 100%;
    display: flex;
    justify-content: space-between;
    i{
        font-size: 16px;
        color: #263849;
    }
    .panelTwo{
        width: 75%;
        // height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: 100%;
            height: 100%;
            background-size: cover;
            background-position: center;
        }
    }
}
}
.Home {
    text-align: center;
    font-family: 'GHEA Grapalat';
    font-style: normal;
    
    header {
        width: 100%;
        height: 242px;
        background-color: #263849;
        text-align: center;
        display: block;
        justify-content: center;

        img {
            width: 215px;
            height: 84px;
            margin-top: 30px;
        }
        .hello-text {
            text-align: center;
            margin-top: 20px;
            h1 {
                font-weight: 400;
                font-size: 20px;
                color: white;
            }
        }
    }
    p {
        width: 589px;
        font-weight: 400;
        font-size: 16px;
        line-height: 30px;
        margin: auto;
        margin-top: 20px;
    }

    .Home-parlament {
        width: 645px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: auto;
        margin-bottom: 300px;
        .Home-item {
            width: 195px;
            height: 94px;
            border: .5px solid #94A3BA;
            box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
            background-color: #fff;
            border-radius: 5px;
            margin-top: 40px;
            display: flex;
            align-items: center;
            padding: 20px 8px;
            cursor: pointer;

            img {
                width: 46px;
                height: 30px;
            }
            h4 {
                width: 123px;
                text-align: start;
                font-weight: 700;
                font-size: 10px;
                line-height: 18px;
                margin-left: 8px;
            }
        }
    }


}